import React, { Component } from 'react';
import Types from 'prop-types';
import MessagesControl from '../messageControl';

import { getControlType, prepValuesAsync } from './utils/requirements';
import { getValidationFailures } from '../common/validation/validation-failures';

export default class Field extends Component {
  static propTypes = {
    name: Types.string.isRequired,
    value: Types.oneOfType([Types.string, Types.number, Types.bool, Types.object]),
    field: Types.shape({
      type: Types.string.isRequired,
      title: Types.string.isRequired,
      required: Types.bool,
      disabled: Types.bool,
      hidden: Types.bool,
      readOnly: Types.bool,
      autoComplete: Types.bool,
      placeholder: Types.string,
      searchPlaceholder: Types.string,
      control: Types.string,
      displayFormat: Types.string,
      helpText: Types.string,
      helpNode: Types.node,
      helpImage: Types.string,
      helpList: Types.array,

      pattern: Types.string,
      minLength: Types.number,
      maxLength: Types.number,

      minimum: Types.number,
      maximum: Types.number,

      uploadProps: Types.shape({}),

      values: Types.arrayOf(Types.shape({})),
      size: Types.string,

      valuesAsync: Types.shape({
        method: Types.string,
        url: Types.string,
      }),

      validationMessages: Types.shape({}),
    }).isRequired,

    fallbackValidationMessages: Types.shape({}),

    // locale: Types.string,
    onChange: Types.func.isRequired,
    onFocus: Types.func,
    onBlur: Types.func,
    errorMessage: Types.string,
    warningMessage: Types.string,
  };

  static defaultProps = {
    // locale: 'en',

    value: undefined,

    errorMessage: undefined,
    warningMessage: undefined,

    fallbackValidationMessages: {},

    onFocus: null,
    onBlur: null,
  };

  constructor(props) {
    super(props);

    const { value, field, errorMessage } = props;

    this.dirty = false;
    this.state = {
      control: field.control ? field.control : getControlType(field),
      validationMessages: {},
      customErrorMessage: errorMessage,
    };

    // TODO we should probably do this at fieldset level, so the value is available
    if (field.valuesAsync) {
      prepValuesAsync(field, value);
    }

    // If the field is required, and only allows one value, set it to that
    // if (field.required && field.enum && field.enum.length === 1) {
    //   const { first } = field.enum;
    //   this.setState({value: first});
    // }
  }

  onFocus(event) {
    if (this.props.onFocus) {
      this.props.onFocus(event);
    }
  }

  onBlur(event) {
    if (this.props.onBlur) {
      this.props.onBlur(event);
    }
    if (this.dirty) {
      this.validateValue(event.target.value);
    }
  }

  onChange(event) {
    if (this.props.onChange) {
      this.props.onChange(event);
      this.dirty = true;
    }

    if (this.props.errorMessage) {
      this.setState({ customErrorMessage: null });
    }

    this.validateValue(event.target.value);
  }

  validateValue(newValue) {
    const { field, fallbackValidationMessages } = this.props;

    const failures = getValidationFailures(newValue, field);

    const validationMessages = {};
    failures.forEach(failure => {
      const messageKey = failure.toLowerCase();

      const message =
        (field.validationMessages && field.validationMessages[messageKey]) ||
        fallbackValidationMessages[messageKey];
      if (message) {
        validationMessages[messageKey] = message;
      }
    });

    this.setState({ validationMessages });
  }

  objectSizeOf = obj => (obj ? Object.keys(obj).length : 0);

  render() {
    const {
      name,
      field,
      value,
      warningMessage,
      // locale
    } = this.props;

    const { control, validationMessages, customErrorMessage } = this.state;

    let messageProps = null;

    if (this.objectSizeOf(validationMessages) > 0) {
      messageProps = {
        type: 'error',
        content: validationMessages,
      };
    } else if (customErrorMessage) {
      messageProps = {
        type: 'error',
        content: customErrorMessage,
      };
    } else if (warningMessage) {
      messageProps = {
        type: 'warning',
        content: warningMessage,
      };
    } else if (field.helpText) {
      messageProps = {
        type: 'info',
        content: field.helpText,
        focusVisible: true,
      };
    } else if (field.helpList) {
      messageProps = {
        type: 'info',
        content: field.helpList,
        focusVisible: true,
      };
    } else if (field.helpImage && control !== 'file') {
      messageProps = {
        type: 'info',
        content: field.helpImage,
        focusVisible: true,
      };
    }

    return (
      <MessagesControl {...messageProps}>
        {control !== 'file' && (
          <label htmlFor="id" className="control-label">
            {field.title}
          </label>
        )}
        {/* <FormControl
            type={field.control},
            name={name},
            label={field.title},
            placeholder={field.placeholder},
            searchPlaceholder={field.searchPlaceholder},
            helpText={field.helpText},
            helpImage={field.helpImage},
            locale={locale},
            uploadProps={field.uploadProps},
            options={field.values},
            value={value},
            onChange={onChange},
            onBlur={onBlur},
            onFocus={onFocus},
            required={field.required},
            disabled={field.disabled},
            readOnly={field.readOnly},
            autoComplete={field.autoComplete},
            minlength={minLength},
            maxlength={maxLength},
            min={minimum},
            max={maximum},
            pattern={pattern},
            textFormat={field.displayFormat},
            validationAsync={validationAsync}
            size={field.size}
        /> */}

        {field.type === 'password' ? (
          <input
            type="password"
            className="form-control"
            placeholder={field.placeholder}
            value={value}
            readOnly={field.readOnly}
            required={field.required}
            disabled={field.disabled}
            onChange={event => this.onChange(event)}
            onBlur={event => this.onBlur(event)}
            onFocus={event => this.onFocus(event)}
            autoComplete={field.autoComplete}
          />
        ) : (
          <input
            type="text"
            className="form-control"
            name={name}
            placeholder={field.placeholder}
            value={value}
            readOnly={field.readOnly}
            required={field.required}
            pattern={field.pattern}
            onChange={event => this.onChange(event)}
            onFocus={event => this.onFocus(event)}
            onBlur={event => this.onBlur(event)}
            disabled={field.disabled}
            autoComplete={field.autoComplete}
          />
        )}
      </MessagesControl>
    );
  }
}



