export default class FieldDocs extends Component {
  state = {
    textInputValue: '',
    inputWithHelpTextValue: '',
    inputWithHelpListValue: '',
    inputWithHelpImageValue: '',
    passwordInputValue: '',
    inputDefaultValue: 'Default value',
    inputWithWarning: '',
    inputWithCustomError: '',
    inputWithValidations: '',
    inputWithValidationsWrongDefaultValue: 'Tex',
    inputWithFallbackValidationsMessages: '',
  };

  textInputComponent = {
    name: 'basic-text-input',
    field: {
      type: 'input',
      title: 'Basic Text Input',
      placeholder: 'Basic Text Input',
    },
    onChange: this.textInputOnChangeHandler.bind(this),
  };

  textInputDefaultValue = {
    ...this.textInputComponent,
  };

  inputComponentWithCustomError = {
    name: 'input-with-error',
    errorMessage: 'Custom error message!',
    field: {
      type: 'input',
      title: 'Custom error',
    },
    onChange: this.textInputOnChangeHandler.bind(this),
  };

  inputComponentWithCustomWarning = {
    name: 'input-with-warning',
    warningMessage: 'This is a warning!',
    field: {
      type: 'input',
      title: 'Custom Warning',
    },
    onChange: this.textInputOnChangeHandler.bind(this),
  };

  textInputWithValidation = {
    name: 'basic-text-input',
    field: {
      type: 'string',
      control: 'text',
      title: 'String validation',
      placeholder: 'Please enter text',
      required: true,
      pattern: '^[A-Z]*$',
      minLength: 4,
      maxLength: 6,
      validationMessages: {
        required: 'String is required',
        minlength: 'Must contain at least 4 characters',
        maxlength: 'Must contain 6 characters or less',
        pattern: 'Must only contain capital letters A to Z',
      },
    },
    onChange: this.textInputOnChangeHandler.bind(this),
  };

  textInputWithFallbackValidationsMessages = {
    name: 'basic-text-input',
    field: {
      type: 'string',
      control: 'text',
      title: 'String validation (with default validation messages)',
      placeholder: 'Please enter text',
      required: true,
      pattern: '^[A-Z]*$',
      minLength: 4,
      maxLength: 6,
    },
    fallbackValidationMessages: {
      required: 'Required',
      minlength: 'The value is too short',
      maxlength: 'The value is too long',
      pattern: 'Incorrect format',
    },
    onChange: this.textInputOnChangeHandler.bind(this),
  };

  textInputWithValidationWithWrongDefaultValue = {
    ...this.textInputWithValidation,
    field: {
      ...this.textInputWithValidation.field,
      title: 'String validation with wrong default value',
    },
  };

  helpMessageLink = 'https://transferwise.com/help/';

  textInputComponentWithHelpText = {
    name: 'text-input',
    field: {
      type: 'input',
      title: 'Help Text',
      placeholder: 'Basic Text Input with Help Text',
      helpNode: (
        <Fragment>
          Some helpful information with a <a href={this.helpMessageLink}>link</a>.
        </Fragment>
      ),
    },
    onChange: this.textInputOnChangeHandler.bind(this),
  };

  textInputComponentWithHelpList = {
    name: 'text-input',
    field: {
      type: 'input',
      title: 'Help List',
      placeholder: 'Basic Text Input with Help List',
      helpList: ['Make sure of this', 'and this', 'and avoid this'],
    },
    onChange: this.textInputOnChangeHandler.bind(this),
  };

  textInputComponentWithHelpImage = {
    name: 'text-input',
    field: {
      type: 'input',
      title: 'Help Image',
      placeholder: 'Basic Text Input with Help Image',
      helpImage,
    },
    onChange: this.textInputOnChangeHandler.bind(this),
  };

  passwordInput = {
    name: 'password-input',
    field: {
      type: 'password',
      title: 'Password',
    },
    onChange: this.textInputOnChangeHandler.bind(this),
  };

  textInputOnChangeHandler(stateValueName) {
    return event => {
      this.setState({ [stateValueName]: event.target.value });
    };
  }

  createStateLink(stateValueName) {
    return value => this.setState({ [stateValueName]: value });
  }

  render() {
    const {
      textInputValue,
      passwordInputValue,
      inputDefaultValue,
      inputWithWarning,
      inputWithCustomError,
      inputWithValidations,
      inputWithValidationsWrongDefaultValue,
      inputWithFallbackValidationsMessages,
      inputWithHelpTextValue,
      inputWithHelpListValue,
      inputWithHelpImageValue,
    } = this.state;

    return (
      <div className="container">
        <section className="section">
          <div className="row">
            <div className="col-md-6">
              <h1 id="field">Field</h1>
              <p>
                The field is the basic building block of our forms, it accepts a set of options to
                configure its presentation, and a model to which it will bind a value.
              </p>
              <p>
                The field controls labelling, validation and help text, alongside the rendering of
                the form control itself.
              </p>
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  name="field-name"
  field="{...}"
  onChange={() => onChange(...)}
  onFocus={() => onFocus(...)}
  onBlur={() => onBlur(...)}
  value="Some data"
/>`}
              </pre>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <h3>Basic Text Inputs</h3>
              <div className="m-t-3" />
              <Field
                name={this.textInputComponent.name}
                value={textInputValue}
                field={this.textInputComponent.field}
                onChange={this.textInputComponent.onChange('textInputValue')}
              />
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  value="${textInputValue}"
  name="${this.textInputComponent.name}"
  onChange={() => onChange(...)}
  field=${JSON.stringify(this.textInputComponent.field, null, '\t')}/>`}
              </pre>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <div className="m-t-3" />
              <Field
                name={this.passwordInput.name}
                value={passwordInputValue}
                field={this.passwordInput.field}
                onChange={this.passwordInput.onChange('passwordInputValue')}
              />
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  name="${this.passwordInput.name}"
  value="${passwordInputValue}"
  onChange={() => onChange(...)}
  field=${JSON.stringify(this.passwordInput.field, null, '\t')}/>`}
              </pre>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <h3>Default value</h3>
              <div className="m-t-3" />
              <Field
                name={this.textInputDefaultValue.name}
                value={inputDefaultValue}
                field={this.textInputDefaultValue.field}
                onChange={this.textInputDefaultValue.onChange('inputDefaultValue')}
              />
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  name="${this.textInputDefaultValue.name}"
  value="${inputDefaultValue}"
  onChange={() => onChange(...)}
  field=${JSON.stringify(this.textInputDefaultValue.field, null, '\t')}/>`}
              </pre>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <h3>Custom errors</h3>
              <p>
                When we want to render a custom error, or render an error on page load, we can pass
                in an additional error message that will be immediately displayed. When the value of
                the field changes the error messages (and state) will be removed as we fall back to
                regular validation.
              </p>
              <p>
                We can also supply warning messages, it is up to the consumer to show/hide these
                messages as the value evolves. Warning messages will be superceded by errors if they
                both exist at the same time.
              </p>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <Field
                name={this.inputComponentWithCustomError.name}
                value={inputWithCustomError}
                field={this.inputComponentWithCustomError.field}
                errorMessage={this.inputComponentWithCustomError.errorMessage}
                onChange={this.inputComponentWithCustomError.onChange('inputWithCustomError')}
              />
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  name="${this.inputComponentWithCustomError.name}"
  value="${inputWithCustomError}"
  field=${JSON.stringify(this.inputComponentWithCustomError.field, null, '\t')}
  errorMessage="${this.inputComponentWithCustomError.errorMessage}"
  onChange={() => onChange(...)}/>`}
              </pre>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <Field
                name={this.inputComponentWithCustomWarning.name}
                value={inputWithWarning}
                field={this.inputComponentWithCustomWarning.field}
                warningMessage={this.inputComponentWithCustomWarning.warningMessage}
                onChange={this.inputComponentWithCustomWarning.onChange('inputWithWarning')}
              />
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  name="${this.inputComponentWithCustomWarning.name}"
  value="${inputWithWarning}"
  field=${JSON.stringify(this.inputComponentWithCustomWarning.field, null, '\t')}
  warningMessage="${this.inputComponentWithCustomWarning.warningMessage}"
  onChange={() => onChange(...)}/>`}
              </pre>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <h3>Validations</h3>
              <div className="m-t-3" />
              <Field
                name={this.textInputWithValidation.name}
                value={inputWithValidations}
                field={this.textInputWithValidation.field}
                onChange={this.textInputWithValidation.onChange('inputWithValidations')}
              />
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  name="${this.textInputWithValidation.name}"
  value="${inputWithValidations}"
  onChange={() => onChange(...)}
  field=${JSON.stringify(this.textInputWithValidation.field, null, '\t')}
  }/>`}
              </pre>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <Field
                name={this.textInputWithValidationWithWrongDefaultValue.name}
                value={inputWithValidationsWrongDefaultValue}
                field={this.textInputWithValidationWithWrongDefaultValue.field}
                onChange={this.textInputWithValidationWithWrongDefaultValue.onChange(
                  'inputWithValidationsWrongDefaultValue',
                )}
              />
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  name="${this.textInputWithValidationWithWrongDefaultValue.name}"
  value="${inputWithValidationsWrongDefaultValue}"
  onChange={() => onChange(...)}
  field=${JSON.stringify(this.textInputWithValidationWithWrongDefaultValue.field, null, '\t')}
  }/>`}
              </pre>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <Field
                name={this.textInputWithFallbackValidationsMessages.name}
                value={inputWithFallbackValidationsMessages}
                field={this.textInputWithFallbackValidationsMessages.field}
                fallbackValidationMessages={
                  this.textInputWithFallbackValidationsMessages.fallbackValidationMessages
                }
                onChange={this.textInputWithFallbackValidationsMessages.onChange(
                  'inputWithFallbackValidationsMessages',
                )}
              />
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  name="${this.textInputWithFallbackValidationsMessages.name}"
  value="${inputWithFallbackValidationsMessages}"
  onChange={() => onChange(...)}
  field=${JSON.stringify(this.textInputWithFallbackValidationsMessages.field, null, '\t')}
  fallbackValidationMessages=${JSON.stringify(
    this.textInputWithFallbackValidationsMessages.fallbackValidationMessages,
    null,
    '\t',
  )}/>`}
              </pre>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <h3>Help information</h3>
              <div className="m-t-3" />
              <Field
                name={this.textInputComponentWithHelpText.name}
                value={inputWithHelpTextValue}
                field={this.textInputComponentWithHelpText.field}
                onChange={this.textInputComponentWithHelpText.onChange('inputWithHelpTextValue')}
              />
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  name="${this.textInputComponentWithHelpText.name}"
  value="${inputWithHelpTextValue}"
  onChange={() => onChange(...)}
  field=${JSON.stringify(this.textInputComponentWithHelpText.field, null, '\t')}/>`}
              </pre>
            </div>
          </div>
          <div className="row">
            <div className="col-md-6">
              <Field
                name={this.textInputComponentWithHelpList.name}
                value={inputWithHelpListValue}
                field={this.textInputComponentWithHelpList.field}
                onChange={this.textInputComponentWithHelpList.onChange('inputWithHelpListValue')}
              />
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  name="${this.textInputComponentWithHelpList.name}"
  value="${inputWithHelpListValue}"
  onChange={() => onChange(...)}
  field=${JSON.stringify(this.textInputComponentWithHelpList.field, null, '\t')}`}
              </pre>
            </div>
          </div>

          <div className="row">
            <div className="col-md-6">
              <Field
                name={this.textInputComponentWithHelpImage.name}
                value={inputWithHelpImageValue}
                field={this.textInputComponentWithHelpImage.field}
                onChange={this.textInputComponentWithHelpImage.onChange('inputWithHelpImageValue')}
              />
            </div>
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<Field
  name="${this.textInputComponentWithHelpImage.name}"
  value="${inputWithHelpImageValue}"
  onChange={() => onChange(...)}
  field=${JSON.stringify(this.textInputComponentWithHelpImage.field, null, '\t')}`}
              </pre>
            </div>
          </div>
        </section>
      </div>
    );
  }
}



