import React, { Component, Fragment } from 'react';
import MessagesControl from './MessagesControl';

class MessagesControlDocs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      helpLink: 'https://reactjs.org',
    };
  }

  render() {
    const { helpLink } = this.state;

    return (
      <Fragment>
        <MessagesControl
          type="warning"
          content={
            <Fragment>
              Check this <a href={helpLink}>link</a>
            </Fragment>
          }
        >
          <input type="text" />
        </MessagesControl>
      </Fragment>
    );
  }
}

export default MessagesControlDocs;



