import React, { isValidElement } from 'react';
import Types from 'prop-types';

import classNames from 'classnames';

import { isString, isArray, isObject } from '../common/validation/type-validators';

const MessagesControl = ({ type, content, focusVisible, children }) => {
  function getMessageContent() {
    if (content) {
      if (isString(content)) {
        if (isImagePath(content)) {
          return <img className="thumbnail m-y-2" src={`${content}`} alt="Help" />;
        }
        return content;
      }
      if (isArray(content) && content.length > 0) {
        return content.map((message, index) => <div key={index.toString()}>{message}</div>);
      }
      if (isValidElement(content)) {
        return content;
      }
      if (isObject(content) && Object.keys(content).length > 0) {
        return Object.keys(content).map(key => <div key={key}>{content[key]}</div>);
      }
    }
    return null;
  }

  function isImagePath(string) {
    return string.match(/.jpg|.jpeg|.png/g);
  }

  const messageContent = getMessageContent();
  const groupCssClasses = classNames('form-group', {
    'has-error': type === MessageType.Error,
    'has-warning': type === MessageType.Warning,
  });
  const messageCssClasses = classNames(
    'alert',
    'alert-detach',
    {
      'alert-info': type === MessageType.Info,
      'alert-warning': type === MessageType.Warning,
      'alert-danger': type === MessageType.Error,
    },
    {
      'alert-focus': focusVisible,
    },
  );

  return (
    <div className={groupCssClasses}>
      {children}
      {messageContent && <div className={messageCssClasses}>{messageContent}</div>}
    </div>
  );
};

export const MessageType = {
  Info: 'info',
  Warning: 'warning',
  Error: 'error',
};

MessagesControl.propTypes = {
  type: Types.string,
  children: Types.oneOfType([
    Types.string,
    Types.node,
    Types.arrayOf(Types.string),
    Types.shape({}),
  ]),
  content: Types.oneOfType([
    Types.string,
    Types.node,
    Types.arrayOf(Types.string),
    Types.shape({}),
  ]),
  focusVisible: Types.bool,
};

MessagesControl.defaultProps = {
  type: MessageType.Info,
  focusVisible: false,
  content: null,
  children: null,
};

export default MessagesControl;



