/* eslint-disable no-console */
import React, { Component } from 'react';
import classNames from 'classnames';
import { Upload, Select, Checkbox } from '..';
import { CompleteStep, ProcessingStep } from '../upload/steps';

const IMAGES = [
  'data:image/svg+xml;base64,',
  'data:image/jpeg;base64,',
  'data:image/jpeg;base64,',
  'data:image/png;base64,',
];
const SIZES = ['sm', 'md', 'lg'];
const COMPONENT_STATUS = ['Error', 'Success'];
const TESTHTTPOTIONS = {
  url: 'http://httpbin.org/post',
  method: 'POST',
};
const ACCEPTED_FORMAT = ['*', 'image/*', 'application/*'];

const KNOBS = {
  firstStepKnobs: [
    {
      type: 'input',
      label: 'Placeholder',
      state: 'usPlaceholder',
      defaultState: 'Drag and drop a file less than 5MB',
    },
    { type: 'input', label: 'Label', state: 'usLabel', defaultState: '' },
    { type: 'input', label: 'Button text', state: 'usButtonText', defaultState: 'Or Select File' },
    {
      type: 'select',
      label: 'Help Image',
      state: 'usHelpImage',
      options: IMAGES,
      defaultState: '',
    },
    { type: 'checkbox', label: 'Disabled', state: 'usDisabled', defaultState: false },
    { type: 'checkbox', label: 'Dropping', state: 'isDropping', defaultState: false },
    {
      type: 'select',
      label: 'Accepted format',
      state: 'usAccept',
      options: ACCEPTED_FORMAT,
      defaultState: ACCEPTED_FORMAT[1],
    },
    {
      type: 'input',
      label: 'Drop Message',
      state: 'usDropMessage',
      defaultState: 'Drop file to start upload',
    },
  ],
  secondStepKnobs: [
    {
      type: 'input',
      label: 'Processing Text',
      state: 'psProcessingText',
      defaultState: 'Uploading...',
    },
    { type: 'input', label: 'Button Text', state: 'psButtonText', defaultState: 'Cancel' },
    {
      type: 'input',
      label: 'Failure text',
      state: 'psFailureText',
      defaultState: 'Upload failed. Please, try again.',
    },
    {
      type: 'input',
      label: 'Success Text',
      state: 'psSuccessText',
      defaultState: 'Upload completed!',
    },
    {
      type: 'select',
      label: 'Component State',
      state: 'processingState',
      options: COMPONENT_STATUS,
      defaultState: '',
    },
  ],
  thirdStepKnobs: [
    {
      type: 'input',
      label: 'Button text',
      state: 'csButtonText',
      defaultState: 'Select other file?',
    },
    {
      type: 'input',
      label: 'Failure text',
      state: 'csFailureText',
      defaultState: 'Upload failed. Please, try again',
    },
    {
      type: 'input',
      label: 'Success Text',
      state: 'csSuccessText',
      defaultState: 'Upload completed!',
    },
    {
      type: 'input',
      label: 'Too Large Message',
      state: 'csTooLargeMessage',
      defaultState: 'csTooLargeMessage',
    },
    {
      type: 'input',
      label: 'Wrong type Message',
      state: 'csWrongTypeMessage',
      defaultState: 'csWrongTypeMessage',
    },
    {
      type: 'select',
      label: 'Component State',
      state: 'completedState',
      options: COMPONENT_STATUS,
      defaultState: '',
    },
  ],
  sharedProps: [
    { type: 'input', label: 'Max Size', state: 'maxSize', defaultState: 5000000 },
    { type: 'input', label: 'Animation Delay', state: 'animationDelay', defaultState: 700 },
    { type: 'select', label: 'Size', state: 'size', options: SIZES, defaultState: 'md' },
    {
      type: 'checkbox',
      label: 'Provide httpOptions ?',
      state: 'httpOptions',
      defaultState: null,
    },
  ],
};

export default class UploadDocs extends Component {
  constructor() {
    super();
    this.state = { ...this.getState(), processingState: null, completedState: null };
  }

  getState = () => {
    const state = [];
    Object.values(KNOBS).map(knob =>
      knob.map(curr => {
        state[curr.state] = curr.defaultState;
        return state;
      }),
    );
    return state;
  };

  getDocs = () => {
    const docs = Object.values(KNOBS)
      .map(knob =>
        knob
          .reduce((acc, curr) => {
            acc.push(`${curr.state} = {"${this.state[curr.state] || ''}"}`);
            return acc;
          }, [])
          .join('\n\r  '),
      )
      .join();

    return (
      <pre className="tw-docs-code ">
        {`<Upload
  onCancel={'console.log'}
  onFailure={'console.log'}
  onStart={'console.log'}
  onSuccess={'console.log'}
  ${docs}
  />`}
      </pre>
    );
  };

  generateInput = knobs => {
    const { type, label, state, options } = knobs;
    switch (type) {
      case 'select':
        return (
          <div className="col-md-6 m-b-2" key={state}>
            <label htmlFor={state} className="control-label">
              {label}
            </label>
            <Select
              id={state}
              selected={
                this.state[state]
                  ? { value: this.state[state], label: this.state[state] }
                  : undefined
              }
              options={options.map(option => ({ value: option, label: option }))}
              onChange={selection =>
                this.setState({ [state]: selection ? selection.value : undefined })
              }
            />
          </div>
        );

      case 'checkbox':
        return (
          <div className="col-md-6 m-b-2" key={state}>
            <label htmlFor={state} className="control-label">
              {label}
            </label>
            <Checkbox
              id={state}
              label={label}
              onChange={value => this.setState({ [state]: value })}
              checked={this.state[state]}
            />
          </div>
        );

      case 'input':
      default:
        return (
          <div className="col-md-6 m-b-2" key={state}>
            <label htmlFor={state} className="control-label">
              {label}
            </label>
            <input
              id={state}
              type="text"
              className="form-control"
              value={this.state[state]}
              onChange={ev => this.setState({ [state]: ev.target.value })}
            />
          </div>
        );
    }
  };

  render() {
    return (
      <div className="container">
        <section className="section">
          {/* Full Component */}
          <div className="row">
            <div className="col-md-6">
              <h2>Upload</h2>
              <span role="img" aria-label="uhm">
                
              </span>
              {getExtraDocs()}
              {this.getDocs()}
            </div>
            <div className="col-md-6">
              <div className="col-xs-12">
                <pre> First Step Props (Image Upload)</pre>
                <div
                  className={classNames({
                    'm-t-3 m-b-4': true,
                    'droppable-dropping': this.state.isDropping,
                  })}
                >
                  <Upload
                    {...this.state}
                    httpOptions={this.state.httpOptions && TESTHTTPOTIONS}
                    onStart={file => console.log('onStart', file)}
                    onSuccess={httpResponse => console.log('onSuccess', httpResponse)}
                    onFailure={httpResponse => console.log('onFailure', httpResponse)}
                    onCancel={() => console.log('onCancel')}
                  />
                </div>
                <div className="row">
                  {KNOBS.firstStepKnobs.map(knob => this.generateInput(knob))}
                </div>
              </div>

              <div className="m-t-5 col-xs-12">
                <pre> Second Step Props (Processing)</pre>
                <div className="m-t-3 m-b-4 droppable droppable-md droppable-processing">
                  <ProcessingStep
                    {...this.state}
                    isError={this.state.processingState === COMPONENT_STATUS[0]}
                    isComplete={false}
                    isProcessing
                    isSuccess={this.state.processingState === COMPONENT_STATUS[1]}
                    onClear={() => {}}
                    onAnimationCompleted={status => {
                      console.log(status);
                    }}
                  />
                </div>
                <div className="row">
                  {KNOBS.secondStepKnobs.map(knob => this.generateInput(knob))}
                </div>
              </div>
              <div className="m-t-5 col-xs-12">
                <pre> Third Step Props (Completed)</pre>
                <div className="m-t-3 m-b-4 droppable droppable-md droppable-complete">
                  <CompleteStep
                    {...this.state}
                    isComplete
                    isImage
                    fileName="image-name.jpg"
                    uploadedImage={IMAGES[3]}
                    onClear={() => {}}
                    isError={this.state.completedState === COMPONENT_STATUS[0]}
                  />
                </div>
                <div className="row">
                  {KNOBS.thirdStepKnobs.map(knob => this.generateInput(knob))}
                </div>
              </div>

              <div className="m-t-5 col-xs-12">
                <pre className="m-b-3"> Shared Props</pre>
                <div className="row">{KNOBS.sharedProps.map(knob => this.generateInput(knob))}</div>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

const getExtraDocs = () => (
  <div>
    <p className=" m-t-5">
      Upload is a component for uploading a single file. It is designed to be used multiple times
      when more than one file is required. Files can be saved immediately by passing in a set of
      http-options.
    </p>
    <p>
      The component also contains an HTML file input which can be submitted in a standard
      multipart/form-data form (which requires a name).
    </p>
    <p>
      A number of event handlers are available during the component lifecycle:
      <br />
      <code className="tw-docs-inline-code">
        <strong>on-start(File)</strong>
      </code>
      - fired when we start processing the file, the file object is passed to the handler,
      containing the size, name, and type of the file.
      <br />
      <code className="tw-docs-inline-code">
        <strong>on-success($httpResponse)</strong>
      </code>
      - fired when we successfully process the file and internal animations are complete. The
      success handler will be passed the response from the $http service if http-options were
      supplied.
      <br />
      <code className="tw-docs-inline-code">
        <strong>on-failure($httpResponse)</strong>
      </code>
      - fired when we fail to process the file after internal animations finish. The failure handler
      will be passed the response from the $http service if http-options were supplied. If you
      supply a max-size the component will validate client side and fail with an $httResponse,
      status: 413 - Request Entity Too Large. For all other errors, interrogate the response and
      supply an appropriate error-message to the component
      <br />
      <code className="tw-docs-inline-code">
        <strong>on-cancel()</strong>
      </code>
      - fired when we reset the control to it&apos;s initial state ready to accept a new file. The
      component&apos;s post upload screen can be fully customised by transcluding new content to
      replace the default behaviour.
    </p>
  </div>
);



